import gsap from 'gsap';
import CSSRulePlugin from "gsap/CSSRulePlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const Animations = () => {
    const animate = document.querySelectorAll('.js-animate');
    const animate2 = document.querySelectorAll('.js-animate2');
    const animate3 = document.querySelectorAll('.js-animate3')
    animate.forEach((anim) => {
        gsap.to(anim , {
            scrollTrigger: {
                trigger: anim,
                start: "center 90%",
            },
            scale: 1,
            opacity: 1,
            duration: 0.8,
            ease : "back.out(1.7)"
        });
    });
    animate2.forEach((anim2) => {
        gsap.to(anim2 , {
            scrollTrigger: {
                trigger: anim2,
                start: "center 90%",
            },
            scale: 1,
            opacity: 1,
            duration: 0.8,
            delay: 0.1,
            ease : "back.out(1.7)"
        });
    });
    animate3.forEach((anim3) => {
        gsap.to(anim3 , {
            scale: 1,
            opacity: 1,
            duration: 0.8,
            delay: 1.3,
            ease : "back.out(1.7)"
        });
    });
    gsap.to(".sm_wrapper", {
        height: "100%",
        duration: 1,
        ease: "none"
    });
    gsap.to(".sm_burger", {
        rotate: 0,
        translateX: 0,
        opacity: 1,
        duration: 0.8,
        delay: 1.3,
        ease: "back.out(1.7)"
    });
}

export default Animations;