// Btn
const burger = document.querySelector('.js-burger');

const Menu = () => {
  // Items
  const menuItem = document.querySelectorAll('.js-menu-item');
  const sideMenu = document.querySelector('.js-side-menu');
  const liItem = document.querySelectorAll('.js-list-item');
  const btnWrapper = document.querySelector('.js-btn-wrapper');

  // Open menu
  burger.addEventListener('click', function () {
    burger.classList.toggle('open');
    sideMenu.classList.toggle('show');
    btnWrapper.classList.toggle('show');
  });

  // Close menu
  liItem.forEach(function(li) {
    li.addEventListener('click', function() {
      burger.classList.remove('open');
      sideMenu.classList.remove('show');
      btnWrapper.classList.remove('show');
    });
  });

  // Current state
  const sections = document.querySelectorAll('.current-state');

  window.addEventListener('scroll', function currentState() {
    let current = '';
  
    sections.forEach(function (section) {
      const sectionTop = section.offsetTop - 50;
      if (pageYOffset >= sectionTop) {
        current = section.getAttribute('id');
      }
    });
  
    menuItem.forEach(function (mi) {
      mi.classList.remove('current-page');
      if (mi.classList.contains(current)) {
        mi.classList.add('current-page');
      }
    });
  });
}

export default Menu;