// Hover
const Hover = () => {
    const hovers = document.querySelectorAll('.js-hover');

    hovers.forEach(function(hover){
        const overlay = hover.querySelector('.js-overlay');

        const innerCursor = document.querySelector('.cursor--small');
        const outterCursor = document.querySelector('.cursor--big');

        hover.addEventListener('mouseenter', function() {
            innerCursor.classList.add('hovered');
            outterCursor.classList.add('hovered');
        });

        hover.addEventListener('mouseleave', function() {
            innerCursor.classList.remove('hovered');
            outterCursor.classList.remove('hovered');
        });

        if (!overlay) return;

        hover.addEventListener('mouseenter', function(e){
            const positionX = hover.getBoundingClientRect().left;
            const positionY = hover.getBoundingClientRect().top;

            const y = e.clientY - positionY;
            const x = e.clientX - positionX;

            overlay.style.left = x + 'px';
            overlay.style.top = y + 'px';
        });

        hover.addEventListener('mouseleave', function(e){
            const positionX = hover.getBoundingClientRect().left;
            const positionY = hover.getBoundingClientRect().top;

            const y = e.clientY - positionY;
            const x = e.clientX - positionX;

            overlay.style.left = x + 'px';
            overlay.style.top = y + 'px';
        });
    });
}

export default Hover;