import gsap from 'gsap';

const CustomCursor = () => {
    const innerCursor = document.querySelector('.cursor--small');
    const outterCursor = document.querySelector('.cursor--big');
    let clientX = -100;
    let clientY = -100;
    const initCursor = () => {
        document.addEventListener('mousemove', e => {
            clientX = e.clientX;
            clientY = e.clientY;
        });
        const render = () => {
            gsap.set(innerCursor, {
                x: clientX,
                y: clientY
            });
            requestAnimationFrame(render);
        }
        render();
    }
    initCursor();

    let lastX = 0;
    let lastY = 0;
    const lerp = (a,b,n) => {
        return (1 - n) * a + n * b;
    }
    
    const bigPosition = () => {
        lastX = lerp(lastX, clientX, 0.15);
        lastY = lerp(lastY, clientY, 0.15);
        gsap.set(outterCursor, {
            x: lastX,
            y: lastY
        });
        requestAnimationFrame(bigPosition);
    }
    bigPosition();
}

export default CustomCursor;