const ImageHandler = () => {
    const profilePicture = new URL('../../public/uploads/me.jpg', import.meta.url);
    const salonPicture = new URL('../../public/uploads/www.ocajne-domacice2012.com.jpg', import.meta.url);
    const digitalAgency = new URL('../../public/uploads/web-design-1-profilePic.jpg', import.meta.url);
    const eghib = new URL('../../public/uploads/eghib-company.jpg', import.meta.url);

    document.querySelector('.js-salon').innerHTML += `<img src="${salonPicture}" loading="lazy" alt="Cosmetic salon" />`;
    document.querySelector('.js-digital-agency').innerHTML += `<img src="${digitalAgency}" loading="lazy" alt="Digital agency" />`;
    document.querySelector('.js-insurance-agency').innerHTML += `<img src="${eghib}" loading="lazy" alt="Medical insurance agency" />`;
    document.querySelector('.js-profile-image').innerHTML += `<img src="${profilePicture}" loading="lazy" alt="My picture" />`;
}

export default ImageHandler;